


.buttonLesson {
    box-shadow: 0px 0px 70px rgba(255, 255, 255, 0.1);
    padding:14px 25px;
    font-size: 25px;
    text-transform: uppercase;
    background-color: brown;
    border-radius: 10px;
    cursor: pointer;
    transition: ease 50ms;
    opacity: 1;
    line-height: 21px;
    font-weight: bold;



}

.buttonLesson:hover{
    opacity: 1;
}


@media (max-width: 1200px) {
    .buttonLesson {
        font-size: 18px;

    }


}



@media (max-width: 800px) {
    .buttonLesson {
        font-size: 16px;
        width: 60%;
    }
}


@media (max-width: 576px) {

    .buttonLesson {
        font-size: 16px;
        width: 80%;

    }

}
@media (max-width: 393px) {
    .buttonLesson {
        font-size: 15px;;
    }
}
@media (max-width: 390px) {
    .buttonLesson {
        font-size: 14px;
    }
}



@media (max-width: 320px) {
    .buttonLesson {
        font-size: 11px;
    }
}

@media (max-width: 280px) {
    .buttonLesson {
        font-size: 11px;
    }
}

